.wrapperContent {
    display: flex;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}
.start {
    text-align: center;
    margin: 60px;
}
.start p {
    margin: 20px;
    font-weight: bold;
}
.content {
    width: 100%;
    margin: 10px;
    background-color: #FBEEC1;
    border-radius: 10px;
}

.wrapperContentM {
    width: 100%;
    background-color: #222629;
    border-radius: 10px;
}
.contentM {
    width: 100%;
    background-color: #FBEEC1;
    border-radius: 0;
}

.contentBlack {
    background-color: #474B4F;
    color: wheat;
}
